@import url("https://fonts.googleapis.com/css2?family=Open+Sans&display=swap");
* {
  box-sizing: border-box;
  font-family: "Open Sans", sans-serif;
}
.App {
  width: 100vw;
  height: 100vh;
}

body {
  background-color: #eee;
  margin: 0;
}
.root {
  display: flex;
  flex-direction: column;
  font-family: "Encode Sans SC", sans-serif;
}

.navbar-brand {
  font-size: 3rem;
  text-decoration: none;
}

.product-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.product {
  background-color: #cfd2e0;
  border-radius: 3px;
  box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.1);
  overflow: hidden;
  margin: 1rem;
  width: 300px;
  position: relative;
}

.product img {
  object-fit: cover;
  height: 450px;
  max-width: 100%;
}

.product-info {
  display: flex;
  padding: 1rem;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 1px;
}

.product-info p {
  margin: 0;
}

.navbar {
  padding: 1rem 3rem;
  background-color: #076152 !important;
}

button {
  cursor: pointer;
}

.buttons {
  display: flex;
  flex-direction: row;
  height: 50px;
  justify-content: flex-end;
}

.btn-primary {
  margin-left: 1.5rem;
}
.search {
  width: 100%;
  justify-content: center;
  background-color: #bdbdbd;
  padding: 1rem;
  display: flex;
  justify-content: center;
}

.search-input {
  height: 30px;
  width: 300px;
  border-radius: 5px;
  outline: none;
  border: none;
}

.container-fluid {
  display: flex;
  flex-direction: column;
}

.details {
  display: flex;
  flex-direction: row;
  align-items: center;
  text-align: center;
}

.image-container {
  align-items: center;
}

.detail-container {
  display: flex;
  flex-direction: column;
}

.loading-image {
  margin: 2rem, auto;
}
